import { configureStore } from "@reduxjs/toolkit"
import { persistStore, persistReducer } from "redux-persist"
import storage from "redux-persist/lib/storage"
import employeeReducer from "./employeeSlice"

// Configuration de la persistance pour le rducteur des employs
const persistConfig = {
  key: "employee", // Spcifiez une cl pour viter la collision avec d'autres parties du store
  storage,
}

const persistedEmployeeReducer = persistReducer(persistConfig, employeeReducer)

export const store = configureStore({
  reducer: {
    employee: persistedEmployeeReducer, // Mettez le rducteur persist ici
  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: false, // Dsactiver le contrle de srialisation si ncessaire
    }),
})

export const persistor = persistStore(store)
